@import url(https://fonts.googleapis.com/css2?family=Codystar:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}
.loading_screen {
  position: fixed;
  top: 0;
  left: 0;
  background: #ddd;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home_screen {
}

.main_div {
  overflow-x: hidden;
}

.header_text {
  font-family: "Codystar", cursive;
  font-weight: 900;
  font-size: 3rem;
  text-align: center;
}

.product_img {
  width: 10rem;
  object-fit: cover;
}

.products .card-body {
  padding: 0.5rem;
}

.icons i {
  padding: 1rem;
  box-shadow: rgba(255, 255, 255, 0.5) 0px 10px 50px;
  border-radius: 50%;
  transition: 0.3s ease;
}
.icons i:hover {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}
.product_divider {
  width: 90%;
  height: 2px !important;
  border-radius: 50px;
  margin: 1.5rem auto;
}

@media (min-width: 768px) {
  .product_divider {
    width: 100%;
  }
}

